		/*'animated/man04_78x150_thumb.png','fixed/man_idle_68x150_thumb.png','animated/man04_78x150_thumb.png',
		'fixed/man_idle_68x150_thumb.png','animated/man04_78x150_thumb.png','fixed/man_idle_68x150_thumb.png',
