					if (isPreviewPhaserMedia.length!==0){
						
						this.load.crossOrigin = 'anonymous'

						let file = isPreviewPhaserMedia[0]
						isPreviewPhaserMedia=[]	
						// png = 'assets/animated/man04_78x150'
						file =  file.substring(0,file.lastIndexOf("_thumb.png"))
						// id = 'man04'
						const id = file.substring( file.lastIndexOf("/") + 1, file.lastIndexOf("_") )
						const tmp = file.substring( file.lastIndexOf("_") + 1, file.length)
						//file = 'assets/animated/man04_78x150.png'
						file += '.png'
						
						// w = 78
						const w = Number(tmp.substring( 0, tmp.indexOf("x")))
						// h = 150
						const h = Number(tmp.substring( tmp.indexOf("x") + 1 , tmp.length))

						this.load.on('complete', () => {
							
							this.anims.remove('PhaserMedia_Animation')
							let allSprites = this.children.list.filter(x => x instanceof Phaser.GameObjects.Sprite)
							allSprites.forEach(x => x.destroy());
							
							this.anims.create ({
								key: 'PhaserMedia_Animation',
								frames: this.anims.generateFrameNumbers(id),
								frameRate: 12,
								repeat: -1
							})
							this.add.sprite(0,0,id).setOrigin(-0.5,0).play('PhaserMedia_Animation')
						}, this)
	
						this.load.spritesheet(id, file, { frameWidth: w, frameHeight: h })
						this.load.start()

					}
					
					
				}, 
			}
		},




		game: {
			type: Phaser.WEBGL,
			audio: {
				noAudio: true
			},
			resolution: window.devicePixelRatio,
			clearBeforeRender: false,
			transparent: false,
			roundPixels: false,
			autoRound:false,
			preserveDrawingBuffer: false,
			desynchronized: false,
			antialias: true,
			fps: { target: 60 },
			scale: {
				fullscreenTarget: "wrapper-phaser",
				//width: DEFAULT_WIDTH,
				mode: Phaser.Scale.FIT,
				autoCenter: Phaser.Scale.CENTER_BOTH
				//width: window.innerWidth,
				//height: window.innerHeight
			},
			parent: 'center-phaser',
			disableContextMenu : true,
			
			scene: 
			{
			preload: function()
			{  
				//***  Load resources ***/
				this.load.crossOrigin = 'anonymous'

				//load FIXED ressources for the all app
				this.load.spritesheet('dragArrow', 'assets/dragArrow.png', { frameWidth:22, frameHeight:22 })
				
	
				//load ressources for the project
				for (const iterator in JSON_properties.resources) {
					const resource = JSON_properties.resources[iterator]
					switch (resource.type) 
					{
					case 'image':
						{	
							this.load.image(resource.id, 'assets/' + resource.src)
							break
						}
					case 'spritesheet':
						{
							this.load.spritesheet(resource.id, 'assets/' + resource.src, { frameWidth :resource.frameWidth, frameHeight: resource.frameHeight })
							break
						}
					}
				}
			},
			
			create: function(){   

				let timedEvent = this.time.addEvent({ delay: 1000, callback: () => { elapsedTime += timedEvent.getElapsedSeconds() }, callbackScope: this, loop: true }); 

				/* THERE MUST BE a background image */
				this.background = this.add.image((1024/2),(764/2), 'background')

				//create FIXED ressources anim for the all app
				this.dragArrowfixedResources = this.anims.create(
					{
						key: 'dragArrow',
						frames: this.anims.generateFrameNumbers('dragArrow'),
						frameRate: 1,
						repeat: -1
					}
				)


				// declare select Box
				this.selection = this.add.rectangle(0, 0, 0, 0, 0x1d7196, 0.5)

				for (let i = 0; i < JSON_properties.resources.length; i++)
				{ 
					// Create spritesheet anims of a sprite => ANIMATIONS SPRITE     
					const resource = JSON_properties.resources[i]
					if (resource.type === 'spritesheet')
					{
						let anim = this.anims.create(
							{
								key: resource.id + '_Animation',
								frames: this.anims.generateFrameNumbers(resource.id),
								frameRate: 12,
								repeat: -1
							}
						)
						//const GUID = Utils.CreateUUID() 
						anim.GUID = resource.GUID
						spriteAnims.push( anim)
					}
				}


				//var shape = this.make.graphics().fillCircle(100, 100, 600);
				//var mask = shape.createGeometryMask();
				//mask.invertAlpha = JSON_properties.layers[i].invertAlpha


					
				//***  Create sprite + spritesheet animation + tween ***//
				for (let i = 0; i <JSON_properties.items.length; i++)
				{
					const sprite = JSON_properties.items[i]
					

					let s = this.add.sprite( sprite.x, sprite.y )
					
					s.GUID = JSON_properties.items[i].GUID

					s.setScale(sprite.scale)
					.setAlpha(sprite.alpha)
					.setTintFill(sprite.tint) 
					.setFlipX(sprite.flipX)
					.setOrigin(0.5)
					.setInteractive({ draggable: true })
					.on('pointerdown', function (){    
						getVueInstance.spriteDrag = true	
						getVueInstance.spriteMouseDown = true
						getVueInstance.isDragHandleArrow  = false
					})
					.on('pointerup', function (){    
						getVueInstance.spriteDrag = false	
						getVueInstance.spriteMouseDown = false
						getVueInstance.isDragHandleArrow  = false
						
					})
					.on("dragstart", function(){
						getVueInstance.spriteDrag = true
						getVueInstance.spriteMouseDown = true
						getVueInstance.isDragHandleArrow  = false
					})	
					.on("drag", function(pointer){
						getVueInstance.spriteDrag = true
						getVueInstance.spriteMouseDown = true
						getVueInstance.isDragHandleArrow  = false
						this.x = parseInt(pointer.x)
						this.y = parseInt(pointer.y)
						JSON_properties.items[i].x =  parseInt(pointer.x)
						JSON_properties.items[i].y =  parseInt(pointer.y)
						
					})
					.on("dragend", function(){
						
						getVueInstance.spriteDrag = false
						getVueInstance.spriteMouseDown = false
						getVueInstance.isDragHandleArrow  = false
					})
					.play(sprite.id + '_Animation')
					
					s.setDataEnabled()        			
					s.data.set('id', sprite.id)
					
					sprites.push (s) 

					// create the sprite tween
					if (JSON_properties.items[i].tween){
						let animation = this.tweens.add(
						{ 
							targets: sprites[i],
							
							//x: JSON_properties.items[i].tween.x1,
							// CRITIC !!! Create bugs with flipX which SEEMS TO work with X tween interpolate ONLY !!!
							// REWRITING tweens implementation seems OBLIGATORY -> investigate DURATION metho
							//y: JSON_properties.items[i].tween.y1,
							
							flipX: JSON_properties.items[i].tween.flipX,
							yoyo: JSON_properties.items[i].tween.yoyo,
							repeat: JSON_properties.items[i].tween.repeat,
							props: {
								x: {
									value: {
										getStart: function () { return JSON_properties.items[i].x },
										getEnd:   function () { return JSON_properties.items[i].tween.x1 }
											
									},
									duration: JSON_properties.items[i].tween.duration 
								}
								
								
								//ease: 'Power1', /* WHY NOT ? */
							}
						})
						animation.GUID = JSON_properties.items[i].GUID 
						animations.push( animation )
					}
				}
				
				//***  Create layers invisible by default and masks ***/
				for (let i = 0; i <JSON_properties.layers.length; i++)
				{
					// each sprite pushed inside ONLY ONE Layer
					// TODO : make sprites[] a parameter in JSON_properties.layers
					const indexLayer = i // Index 0 is the layer for Design Mode
					layers.push(this.add.layer())
					if (i === 0){
						layers[indexLayer].add([sprites[0]])
					}
					if (i === 1){
						layers[indexLayer].add([sprites[2],sprites[3],sprites[4]])
					}

					graphics.push(this.add.graphics())
					graphicsMask.push(this.make.graphics().fillStyle(0x000000))

					for (let j = 0; j <JSON_properties.layers[i].zones.length; j++)
					{   
						const polygon = new Phaser.Geom.Polygon(
							JSON_properties.layers[i].zones[j].coords
						)
						layers[indexLayer].mask = new Phaser.Display.Masks.BitmapMask(this, graphicsMask[indexLayer].fillPoints(polygon.points, true))
						// invertAlpha = true -> HIDE inside masks // invertAlpha = false -> SHOW ONLY inside masks
						layers[indexLayer].mask.invertAlpha = JSON_properties.layers[i].invertAlpha
						masks[indexLayer] = layers[indexLayer].mask
						// we start in Design mode => masks are hidden 
						layers[indexLayer].clearMask()
						
						// Draw layers filled with color (invisible by default) -> fillPoints + strokePoints
						graphics[indexLayer]
						.fillStyle(JSON_properties.layers[i].color, 0.4)
						.lineStyle(0, JSON_properties.layers[i].color)
						.fillPoints(polygon.points, true)
						.strokePoints(polygon.points, true)
						.setVisible(true) 
					}
				}

				//*** canvas events***/
				this.input	
				.on('pointerdown', handlePointerDown, this)
				.on('pointermove', handlePointerMove, this)
				.on('pointerup', handlePointerUp, this)
				//.on('gamein', gamein, this)
				//.on('gameout', gameout, this)
				.on('pointerdownoutside', pointerdownoutside, this)
				.on('pointerupoutside', pointerupoutside, this)
				function pointerdownoutside (pointer)
				{
					getVueInstance.sceneMouseDown = false
					getVueInstance.spriteMouseDown = false
					getVueInstance.isDrag  = false
					getVueInstance.isDragHandleArrow  = false
					this.selection.x = pointer.x
					this.selection.y = pointer.y
				}
				function pointerupoutside ()
				{
					getVueInstance.sceneMouseDown = false
					getVueInstance.spriteMouseDown = false
					this.selection.width = 0
					this.selection.height = 0
				}
				function handlePointerDown (pointer)
				{
					if (getVueInstance.isDragHandleArrow === true){
						return
					}

					getVueInstance.spriteInSelection = false
					
					clone_spritesSelected = []
					for (let i = 0; i < spritesSelected.length; i++)
					{
						clone_spritesSelected.push(spritesSelected[i])
					}
					
					if (getVueInstance.spriteMouseDown && !getVueInstance.spriteDrag)
					{		
						

						getVueInstance.sceneMouseDown = false
						//getVueInstance.sceneMouseUp = false

						this.selection.x = pointer.x
						this.selection.y = pointer.y
						this.selection.width = 0
						this.selection.height = 0
						const selectionRect = new Phaser.Geom.Rectangle(
							pointer.x,
							pointer.y,
							0,
							0
						)
						SelectedBox(selectionRect) 
						handlePointerMove.bind(this)
						
						if (clone_spritesSelected.length > 1)
						{
							for (let i = 0; i < clone_spritesSelected.length; i++)
							{
								if (clone_spritesSelected[i].GUID === spritesSelected[0].GUID)
								{
									getVueInstance.spriteInSelection = true
									spritesSelected = []
									for (let i = 0; i < clone_spritesSelected.length; i++)
									{
										spritesSelected.push(clone_spritesSelected[i])
									}

									SelectedBox(getVueInstance.clone_selectionRect)
									handlePointerMove.bind(this)
									return
								}
								
							}
						}
						
						getVueInstance.spriteInSelection = false

						clone_spritesSelected = []
						for (let i = 0; i < spritesSelected.length; i++)
						{
							clone_spritesSelected.push(spritesSelected[i])
						}
		
					} else {

						spritesSelected = []
						getVueInstance.sceneMouseDown = true
						//getVueInstance.sceneMouseUp = false

						this.selection.x = pointer.x
						this.selection.y = pointer.y
						this.selection.width = 0
						this.selection.height = 0
						const selectionRect = new Phaser.Geom.Rectangle(
							pointer.x,
							pointer.y,
							0,
							0
						)
						SelectedBox(selectionRect) 
						handlePointerMove.bind(this)
					}
					
					// eslint-disable-next-line no-//console
					//console.log('scene pointerdown')
				}
				function handlePointerMove(pointer)
				{
					if (getVueInstance.isDragHandleArrow === true){
						return
					}

					getVueInstance.sceneMouseDown = false
					
					if (getVueInstance.spriteMouseDown || !pointer.isDown){
						return
					}

					

					//that.mouseMove = true

					const dx = pointer.x - pointer.prevPosition.x
					const dy = pointer.y - pointer.prevPosition.y

					this.selection.width += dx
					this.selection.height += dy
					

					// create a new Rectangle
					const selectionRect = new Phaser.Geom.Rectangle(
						this.selection.x,
						this.selection.y,
						this.selection.width,
						this.selection.height
					)

					// check if width or height is negative
					// and then adjust
					if (selectionRect.width < 0)
					{
						selectionRect.x += selectionRect.width
						selectionRect.width *= -1
					}
					if (selectionRect.height < 0)
					{
						selectionRect.y += selectionRect.height
						selectionRect.height *= -1
					}

					getVueInstance.clone_selectionRect = selectionRect

					// Do Something with selected box
					SelectedBox(selectionRect)
				}
				function handlePointerUp()
				{
					if (getVueInstance.isDragHandleArrow === true){
						return
					}

					getVueInstance.spriteInSelection = false
					this.selection.width = 0
					this.selection.height = 0
			
					if (spritesSelected.length === 0)
					{
						// Empty 
						const selectionRect = new Phaser.Geom.Rectangle(
							this.selection.x,
							this.selection.y,
							this.selection.width,
							this.selection.height
						)
						SelectedBox(selectionRect)
					}
					getVueInstance.spriteMouseDown = false
					getVueInstance.sceneMouseDown = false
				}
				function SelectedBox(selectionRect) 
				{
					// use the new Rectangle to check for overlap
					const selected = sprites.filter(sprite => {
						const rect = sprite.getBounds()
						return Phaser.Geom.Rectangle.Overlaps(selectionRect, rect)
					})

					// Draw new sprites selection shapes
					spritesSelected = []
					for (let i = 0; i < selected.length; i++){
						spritesSelected.push(selected[i])
					}
					propertiesUpdate()
				}
				function propertiesUpdate() {
					// single selection => update GUI properties
					if (spritesSelected.length === 1){ 
						getVueInstance.opacity  = spritesSelected[0].alpha * 100
						getVueInstance.flipX  = spritesSelected[0].flipX
						getVueInstance.scale_Sprite  = parseInt(spritesSelected[0].scale * 100)
						
					}
				}

				// declare debug for the update event
				this.infos = this.add.text(10, 10, '', { fontFamily: 'Arial', fontSize: 14, color: '#00bb00' })  
				
				//declare canvas for the update event
				this.canvas = this.add.graphics({ lineStyle: {  width: 1, color: 0x77B5FE, alpha: 1 } })	
			}, 





			update: function(){   
				this.canvas.clear()
				
				//only once
				if ((this.designMode==null) ){
					this.designMode = true
					for (let i = 0; i < animations.length; i++){
						animations[i].restart().seek(0.01).pause()
					}
					for (let i = 0; i < spriteAnims.length; i++){
						spriteAnims[i].pause()
					}
				}

				if (isDragFromToolBox.length){
					pngLazyLoad.call(this, isDragFromToolBox[0])
					isDragFromToolBox=[]
				}
								
				function pngLazyLoad(png) {
					this.load.crossOrigin = 'anonymous'
					// png = 'assets/animated/man04_78x150'
					png =  png.substring(0,png.lastIndexOf("_thumb.png"))
					// id = 'man04'
					const id = png.substring( png.lastIndexOf("/") + 1, png.lastIndexOf("_") )
					const tmp = png.substring( png.lastIndexOf("_") + 1, png.length)
					// png = 'assets/animated/man04_78x150.png'
					png += '.png'
					// w = 78
					const w = Number(tmp.substring( 0, tmp.indexOf("x")))
					// h = 150
					const h = Number(tmp.substring( tmp.indexOf("x") + 1 , tmp.length))

					// see below this.load.start
					// alert('0')
					let GUID = Utils.CreateUUID()
					this.load.spritesheet(id, png, { frameWidth: w, frameHeight: h})
					// alert('1')


						this.load.once('complete', ()=>{
							
							// alert('3')
							spriteAnims.push( 
								this.anims.create ({
									key: id + '_Animation',
									frames: this.anims.generateFrameNumbers(id),
									frameRate: 12, // TODO
									repeat: -1
								})
							)
							
							// Add the new item to original JSON
							
							JSON_properties.items.push (
								{ 
								GUID: GUID, id: id, x: 200, y: 100, tint: 0x000000, alpha:1, flipX:false, scale:0.5,
								tween: { x1:600, y1:100, duration:5000, flipX:true, yoyo:true, repeat:-1 }
								}
							) 
							
							//const indexJSON_properties_items = JSON_properties.items.length-1
							//destroy this and REPLACE with an index property in sprite with value = i
							//const indexSprites = sprites.length-1
							
							const s = this.add.sprite( -200, -200 )
							.setScale(0.5)
							.setOrigin(0.5)
							.setFlipX(false)
							.setInteractive({ draggable: true })
							.on('pointerdown', function (){    
								getVueInstance.spriteDrag = true	
								getVueInstance.spriteMouseDown = true
								getVueInstance.isDragHandleArrow  = false
							})
							.on('pointerup', function (){    
								getVueInstance.spriteDrag = false	
								getVueInstance.spriteMouseDown = false
								getVueInstance.isDragHandleArrow  = false
							})
							.on("dragstart", function(){
								getVueInstance.spriteDrag = true
								getVueInstance.spriteMouseDown = true
								getVueInstance.isDragHandleArrow  = false
							})	
							.on("drag", function(pointer){
								getVueInstance.spriteDrag = true
								getVueInstance.spriteMouseDown = true
								getVueInstance.isDragHandleArrow  = false
								this.x = parseInt(pointer.x)
								this.y = parseInt(pointer.y)


								JSON_properties.items.filter(s  => s.GUID === GUID)[0].x =  parseInt(pointer.x)
								JSON_properties.items.filter(s  => s.GUID === GUID)[0].y =  parseInt(pointer.y)
								
							})
							.on("dragend", function(){
								getVueInstance.spriteDrag = false
								getVueInstance.spriteMouseDown = false
								getVueInstance.isDragHandleArrow  = false
							})
							.play(id + '_Animation')

							s.setDataEnabled()        			
							s.data.set('id', id + '_' + '9999')
							s.GUID = GUID 
							
							sprites.push (s) 
							
							//let allSprites = getVueInstance.children.list.filter(x => x instanceof Phaser.GameObjects.Sprite);
				//allSprites.forEach(x => console.log(x));

							
							//const result = JSON_properties.items.filter(s  => s.GUID === GUID)[0].x
							//console.log(result)
							//console.table(sprites.filter((s)  => s.data.get(GUID) === GUID))
							animations.push (
								this.tweens.add(
								{
									GUID: GUID,
									targets: sprites.filter(i  => i.GUID === GUID)[0], //sprites[sprites.length -1],
									

									flipX: JSON_properties.items.filter(i  => i.GUID === GUID)[0].tween.flipX,
									yoyo: JSON_properties.items.filter(i  => i.GUID === GUID)[0].tween.yoyo,
									repeat: JSON_properties.items.filter(i  => i.GUID === GUID)[0].tween.repeat,
									props: {
										x: {
											value: {
												getStart: function()  { return JSON_properties.items.filter(i  => i.GUID === GUID)[0].x },
												getEnd:   function()  { return JSON_properties.items.filter(i  => i.GUID === GUID)[0].tween.x1 }
											
											},
											duration: JSON_properties.items.filter(i  => i.GUID === GUID)[0].tween.duration 
										}
										
										//ease: 'Power1', /* WHY NOT ? */
									},
									paused: true
								})
							)	
							
							// anim.pause() must be paused	
							
						}, this);   

					// alert('2')
					// call complete() just ABOVE ! 
					this.load.start()

					//const result = sprites.filter(s  => s.GUID === GUID)
					//console.log(result)

					spritesSelected.push(sprites[sprites.length - 1])

					// alert('3') IS ABOVE !!!	
					// this is alert('4')
					this.input.on('pointermove', (pointer) => {
						if (( getVueInstance.overCanvas ) && ( getVueInstance.isDnD_FromToolBox ))	{
							sprites[sprites.length - 1].setPosition(pointer.x, pointer.y)
							const j =  JSON_properties.items[sprites.length - 1]
							j.x = parseInt(pointer.x)
							j.y = parseInt(pointer.y)
							j.tween.x1 = parseInt(pointer.x) + 200
							j.tween.y1 = parseInt(pointer.y)
						}
					})
				}
				
				

				if (getVueInstance.debug){
					let debug = ''

					let minutes = Math.floor(elapsedTime / 60);
					let seconds = Math.floor(elapsedTime - (minutes * 60));
					debug += "elapsedTime: " +  minutes + ':' +  seconds + "\n";

					debug+= 'MouseX : ' + MouseX + ' MouseY:' + MouseY + "\n"
					debug+= 'sprites[3] pointermove x: ' + sprites[3].x + ' y:' + sprites[3].y + "\n"
					
					debug+= 'isDnD_FromToolBox: ' + getVueInstance.isDnD_FromToolBox + "\n"
					debug+= 'overCanvas: ' + getVueInstance.overCanvas + "\n"

					debug+= 'isTweening[3]: ' + this.tweens.isTweening(sprites[3]) + "\n"
					if (this.tweens.getTweensOf(sprites[3],true)[0]){
						debug+= 	'tween[3] : Start=' + this.tweens.getTweensOf(sprites[3],true)[0].data[0].start + 
										' End=' + this.tweens.getTweensOf(sprites[3],true)[0].data[0].end
						+ "\n" + "\n"
					}

					//debug+= 'Is tweening[3]: ' + getVueInstance.mouse.x + "\n"

					//let allSprites = this.children.list.filter(x => x instanceof Phaser.GameObjects.Sprite)
					debug+= (this.game.loop.actualFps | 0) + "fps \n" 
					debug+= 'Nb Sprites: ' + sprites.length + "\n" 
					debug+= 'designMode: ' + getVueInstance.designMode + "\n" 
					debug+= 'displayLayers: ' + getVueInstance.displayLayers + "\n" 
					
					debug+= 'sceneMouseDown: ' + getVueInstance.sceneMouseDown +  "\n" 
					//debug+= 'sceneMouseUp: ' + getVueInstance.sceneMouseUp +  "\n" 
					
					debug+= 'spriteMouseDown: ' + getVueInstance.spriteMouseDown +  "\n" 
					debug+= 'spriteInSelection: ' + getVueInstance.spriteInSelection +  "\n" 
					debug+= 'spriteDrag: ' + getVueInstance.spriteDrag +  "\n" 
					debug+= 'isDragHandleArrow: ' + getVueInstance.isDragHandleArrow +  "\n\n" 
							
					debug+= 'spritesSelected: ' + "\n" 
					for (let i = 0; i < spritesSelected.length; i++)
					{
						debug+= spritesSelected[i].data.get('id') + " || " + spritesSelected[i].GUID + "\n" 
					}

					debug+= 'clone_spritesSelected: ' + "\n" 
					for (let i = 0; i < clone_spritesSelected.length; i++)
					{
						debug+= clone_spritesSelected[i].data.get('id') + " || " + clone_spritesSelected[i].GUID  + "\n" 
					}
					
					debug+= 'isDragFromToolBox: ' + JSON.stringify(isDragFromToolBox)  + "\n"
					
					debug+= "\n" + 'spriteDragArrows: ' + "\n" 
					for (let i = 0; i < spriteDragArrows.length; i++)
					{
						debug+= JSON.stringify(spriteDragArrows[i].GUID) + "\n" 
					}
					


					this.infos.setText(debug)
				} else {
					this.infos.setText('')
				}
				
				if (fullscreen){

					
						this.designMode = false
					
						// masks or not ?
						for (let i = 0; i < JSON_properties.layers.length; i++)
						{
							this.designMode ? layers[i].clearMask() : layers[i].setMask(masks[i])
						}

						// sprites play or pause => stop ?
						for (let i = 0; i < JSON_properties.items.length; i++)
						{
							const sprite = JSON_properties.items[i]
							this.designMode ? sprites[i].setFrame(0).pause() : sprites[i].play(sprite.id + '_Animation')
						}
						
						
						// animations or not ?
						for (let i = 0; i < animations.length; i++)
						{
							this.designMode ? animations[i].pause() : animations[i].restart()
						}
						// this.scale.setGameSize(sceneWidth*2, sceneHeight*2)
						//this.scale.setGameSize(window.innerWidth, window.innerHeight)
						
						fullscreen = false
						this.scale.startFullscreen()	
					
				}


				/*** SELECTED OBJECTS ***/
				const g = this.canvas
				if ( getVueInstance.designMode ) {
					const  timer = (Date.now() % 600+1) / 600

					// Hack blendmodes : why not ?
					//g.setBlendMode(Phaser.BlendModes.MULTIPLY);

					/* draw sprites SELECTION ANIMATED and other sprites (OUT OF SELECTION) handles  ***/
					for (let i = 0; i < sprites.length; i++){
						if ((i == sprites.length - 1) && ( !getVueInstance.overCanvas ) && ( getVueInstance.isDnD_FromToolBox )){
							// isDnD_FromToolBox && !overCanvas (= drag NEW sprite)
							// don't show any type of handles
							continue
						} 
						const bounds = sprites[i].getBounds()
						const rect = new Phaser.Geom.Rectangle(bounds.x, bounds.y, bounds.width, bounds.height)
						const points = []
						Phaser.Geom.Rectangle.Decompose(rect, points)	
						
						// draw OUT OF SELECTION sprite
						if (spritesSelected.includes(sprites[i])===false){
							// draw sprite selection rect
							g.lineStyle(2, 0x77B5FE, 1)
							g.fillStyle( 0x77B5FE, 1)
							g.strokeRectShape(bounds)
							// draw handles	
							for(let j = 0; j < points.length; j++){
								g.lineStyle(2, 0x77B5FE, 1)
								g.fillRect((points[j].x)-4,(points[j].y)-4, 8, 8)

								g.lineStyle(1, 0xFFFFFF, 1)
								g.strokeRect((points[j].x)-4,(points[j].y)-4, 8, 8)	
							}
						// draw spritesSelected									
						} else {
							if (!getVueInstance.spriteDrag  ){
								const poly=[]
								for(let j = 0; j < points.length; j++)
									poly.push({x:Number(points[j].x), y:Number(points[j].y)})

								// Draw ANIMATED dashed selection
								g.lineStyle(2, 0x000000, 1)
								g.fillStyle( 0x000000, 1)
								Utils.drawDashedPolygon(g, poly, 0, 0, 0, 8, 4,timer)
								//draw handles
								for(let j = 0; j < points.length; j++)
								{
									g.lineStyle(2, 0x000000, 1)
									g.fillStyle( 0x000000, 1)
									g.fillRect(points[j].x-4,points[j].y-4, 8, 8)	

									g.lineStyle(1, 0xFFFFFF, 1)
									g.strokeRect((points[j].x)-4,(points[j].y)-4, 8, 8)	
								}
							}


						}
					}

					// Draw arrows from tweens animations	
					for (let i = 0; i < animations.length; i++){
						// isDnD_FromToolBox && !overCanvas
						// don't show ARROWS of the future (?) animation sprite outside canvas zone
						if ((i == animations.length - 1) && ( !getVueInstance.overCanvas ) && ( getVueInstance.isDnD_FromToolBox ))
							continue
						
						//console.log(animations[i]) /* explore tween*/

						g.lineStyle(3, 0x77B5FE)
						Utils.Arrow(
							g, 
							JSON_properties.items[i].x,
							JSON_properties.items[i].y,
							JSON_properties.items[i].tween.x1,
							JSON_properties.items[i].y
							//animations[i].targets[0].x, 
							//animations[i].targets[0].y, 
							//animations[i].data[0].end, //only 'x' value for the Archi2D app
							//animations[i].targets[0].y
						)
						
						g.lineStyle(3, 0x77B5FE)
						Utils.dashedLine(
							g,
							JSON_properties.items[i].tween.x1,
							JSON_properties.items[i].y,
							JSON_properties.items[i].x,
							JSON_properties.items[i].y,
							[15,5,5,5]
						);			
					}
							

					//draw selected arrow handle for Drag & Drop if ONLY 1 media is selected
					if (spritesSelected.length === 1){
						
						//reinitialize
						if  ((getVueInstance.spriteDrag) || (spriteDragArrows.length ===0 ) && ((dragArrows.indexOf( spritesSelected[0].GUID) !==-1) || (spriteDragArrows.length === 0))){
							for (let i = 0; i < spriteDragArrows.length; i++){
								spriteDragArrows[i].removeFromDisplayList()
								spriteDragArrows[i].removeInteractive()
							}
							spriteDragArrows = []

							dragArrows = []
							const point = JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0]
							dragArrows.push({ GUID: spritesSelected[0].GUID, x: Number(point.tween.x1), y: Number(point.y)})

							for (let i = 0; i < dragArrows.length; i++){
								let s = this.add.sprite( dragArrows[i].x, dragArrows[i].y )
								.setOrigin(0.5)
								.setInteractive({ draggable: true })
								.on('pointerdown', function (){    
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false	
									getVueInstance.spriteMouseDown = true
									getVueInstance.isDragHandleArrow = true
								})
								.on('pointerup', function (){    
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false	
									getVueInstance.spriteMouseDown = false
									getVueInstance.isDragHandleArrow = false
									
									
								})
								.on("dragstart", function(){
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false
									getVueInstance.spriteMouseDown = true
									getVueInstance.isDragHandleArrow = true
								})	
								.on("drag", function(pointer){
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false
									getVueInstance.spriteMouseDown = true
									getVueInstance.isDragHandleArrow = true
									
									this.x = parseInt(pointer.x)
									this.y = parseInt(point.y) // only x for this app
									
									point.tween.x1 =  parseInt(pointer.x)					
								})
								.on("dragend", function(){
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false
									getVueInstance.spriteMouseDown = false
									getVueInstance.isDragHandleArrow = false
									
								})
								.play(this.dragArrowfixedResources)
								s.setDataEnabled()        			
								s.data.set('GUID', spritesSelected[0].GUID)
								s.GUID = spritesSelected[0].GUID
								s.data.set('id', 'dragArrowfixedResources')
								spriteDragArrows.push (s) 
							}
						}
					} else {
						for (let i = 0; i < spriteDragArrows.length; i++){
							spriteDragArrows[i].removeFromDisplayList()
							spriteDragArrows[i].removeInteractive()
						}
						spriteDragArrows = []
					}
				} else {
					// not in designMode
					for (let i = 0; i < spriteDragArrows.length; i++){
						spriteDragArrows[i].removeFromDisplayList()
						spriteDragArrows[i].removeInteractive()
					}
					spriteDragArrows = []
				}			
			},
			getVueInstance: function() {
				getVueInstance = this
			},
			changeColor : function (colors){
				for (let i = 0; i < spritesSelected.length; i++)
					sprites[sprites.indexOf(spritesSelected[i])].setTintFill( '0x' + colors.hex.substring(1) )
			}, 
