  methods: {
	showToolTipPhaser(index){
		const x = this.$refs.pngFile[index].getBoundingClientRect().left - (this.$refs.pngFile[index].getBoundingClientRect().width / 2) -10
		const y = (this.$refs.pngFile[index].getBoundingClientRect().top - this.$refs.pngFile[index].getBoundingClientRect().height) - 110 
		
							
		if ((isPreviewPhaserMedia.length===0) || (isPreviewPhaserMedia[0]!==this.$refs.pngFile[index].id)){
			isPreviewPhaserMedia.push('assets/' + this.$refs.pngFile[index].id)
		}
		let elem = document.getElementById('TooltipPhaser') 
		elem.style.visibility = 'visible'
		elem.style.position = 'absolute'
		elem.style.left = x + "px"
		elem.style.top = y + "px"
	},     
	hideToolTipPhaser(){
		let elem = document.getElementById('TooltipPhaser') 
		elem.style.visibility = 'hidden'
	},

	getPngFile (f) {
      return "assets/"+ f
    },
	
	toolbox_dragStart(item){
		this.isDnD_FromToolBox=true
		this.phaserDropped = false
		spritesSelected = []
		clone_spritesSelected = []
		getVueInstance.spriteDrag = true
		isDragFromToolBox.push(item) 					
	},
	moveOnCanvas(){
		this.overCanvas = true
		if (( this.overCanvas ) && ( this.isDnD_FromToolBox ))	{
			sprites[sprites.length-1].setVisible(true)
		}		
	},
	leaveCanvas(){
		this.overCanvas = false
		if ( this.isDnD_FromToolBox )	{
			sprites[sprites.length-1].setVisible(false)
		}	
	},
	toolbox_dragEnd() {
		if (!this.phaserDropped){
			// if drop OUTSIDE phaser : destroy sprite and animation
			JSON_properties.items.pop()
			animations[animations.length-1].stop()
			sprites[sprites.length-1].removeFromDisplayList()
			animations.pop()
			sprites.pop()
		}
		this.isDnD_FromToolBox = false
		getVueInstance.spriteDrag = false
		//this.phaserDropped = false
			
	},
	PhaserDropped() {
      this.phaserDropped = true
    },
    changeDesignMode: function() {	
		this.designMode = !this.designMode
		this.designMode ? this.displayLayers = true : this.displayLayers = false	
		
		// TODO : Pour les 3 boucles suivantes, elles devraient être lié aux sprites sur la scene et non sur le Json d'origine 
		// quoique NON !, il faut s'assurer de bien modifier le JSON FINAL(? à étudier...) à chaque ajout ou suppression 
		// de sprite ou modif de propriété, layers ou animation  !


		// displayLayers => masks, or not ?
		// clearmask en design mode, parce ce que sans, on ne pourrait pas voir certains sprites sous "obstacle"'
		for (let i = 0; i <JSON_properties.layers.length; i++){
			graphics[i].setVisible(this.displayLayers)
			this.designMode ? layers[i].clearMask() : layers[i].setMask(masks[i]) 
		}

		//en design mode, remet le sprite en position de départ
		for (let i = 0; i < JSON_properties.items.length; i++){
			if (this.designMode ) sprites[i].setFlipX(JSON_properties.items[i].flipX).setFrame(0)
		}
		// spriteAnims pause or resume
		for (let i = 0; i < spriteAnims.length; i++){
			this.designMode ? spriteAnims[i].pause() : spriteAnims[i].resume()
		}
		// animation tweens pause or resume
		for (let i = 0; i < animations.length; i++){
			// seek(0) is stoppping animation : seek(0.01) is ok !
			this.designMode ? animations[i].restart().seek(0.01).pause() : animations[i].restart().resume()
		}
    },
	// TODO : non appelé, je pense, devrait être sur : on canvas mousedown
	DesignMode: function() {
		
		this.designMode  = true 
		this.displayLayers = true

		//this.game.scene.showLayers.call(this.game.scene)	
		//this.game.scene.changeDesignMode.call(this.game.scene)				
		
    },
	DebugMode: function() {
		this.debug = !this.debug
    },
    showLayers: function() {
		this.displayLayers = !this.displayLayers
		for (let i = 0; i <JSON_properties.layers.length; i++){
			graphics[i].setVisible(this.displayLayers)
		}
		//this.game.scene.showLayers.call(this)	
    },
	changeFullscreen: function() {
        fullscreen = true
    },
	changeColor: function(colors) {
		this.game.scene.changeColor.call(this.game.scene,colors)
	},
	opacityError: function(type, msg) {
		switch (type) {
			case ERROR_TYPE.VALUE:
				break
			case ERROR_TYPE.INTERVAL:
				break
			case ERROR_TYPE.MIN:
				break
			case ERROR_TYPE.MAX:
				break
			case ERROR_TYPE.ORDER:
				break
		}
		this.opacityErrorMsg = msg
	},
	setOpacity: function(){
		if (!this.designMode) return
		this.opacityErrorMsg = ''
		for (let i = 0; i < spritesSelected.length; i++){
			JSON_properties.items[sprites.indexOf(spritesSelected[i])].alpha = this.opacity / 100
			sprites[sprites.indexOf(spritesSelected[i])].setAlpha( this.opacity / 100 )
		}
		
	},
	setFlipX: function(){
		if (!this.designMode) return
		for (let i = 0; i < spritesSelected.length; i++){
			JSON_properties.items[sprites.indexOf(spritesSelected[i])].flipX = !sprites[sprites.indexOf(spritesSelected[i])].flipX
			sprites[sprites.indexOf(spritesSelected[i])].setFlipX( !sprites[sprites.indexOf(spritesSelected[i])].flipX )
			
		}
	},
	test: function(){
		isDragFromToolBox.push('tree') 
		
		//this.game.scene.test.call(this.game.scene)	
		
	},
	click_div: function(){
			//alert('in canvas div')
	},

	Recorder: function(canvas)  {
		let fps = 60
		let videoStream = canvas.captureStream(fps)
		let mediaRecorder = new MediaRecorder(videoStream)
		let videoURL=""

		let chunks = []
			mediaRecorder.ondataavailable = function(e) {
			chunks.push(e.data)
		}
	
		mediaRecorder.onstop = function() {
			let blob = new Blob(chunks, {
			'type': 'video/mp4'
			})
			chunks = []
			videoURL = URL.createObjectURL(blob)
			//console.log("mp4 video url:", videoURL);
			let myReader = new FileReader()
			myReader.readAsDataURL(blob)

			myReader.addEventListener("loadend", function(e) {
				document.getElementById('video').src = e.srcElement.result
				document.getElementById('video').hidden = true
				const link = document.createElement("a")
				link.href = videoURL
				link.download = JSON_properties.project
				link.click();
			});
		}

		mediaRecorder.ondataavailable = function(e) {
			chunks.push(e.data)
		}

		this.start = () => mediaRecorder.start()
		this.stop = () => 	mediaRecorder.stop()
		this.pause = () => mediaRecorder.pause()
		this.resume = () => mediaRecorder.resume()
		this.getUrl = () => videoURL
	},
	initRecord: function() {
		let canvas = document.querySelector('#center-phaser > canvas');
		let rec = new getVueInstance.Recorder(canvas)
		setTimeout(rec.start, 60)
		setTimeout(rec.stop, JSON_properties.mp4_duration);
	}
  },
}




</script>


